import React, { Component } from 'react';
import {
  Switch,
  Route
} from 'react-router-dom'
import App from './App';
import { withCookies } from 'react-cookie';

class TopApp extends Component {
  render() {
    return (
      <div className="TopApp">
        <Switch>
          <Route path="/" render={() => (<App cookies={this.props.cookies}/>)}/>
        </Switch>
      </div>
    );
  }
}

export default withCookies(TopApp);
