import React, { Component } from 'react';
import './sc.css';

import axios from 'axios';
import Header from "./components/Header";
import ProductList from "./components/ProductList";
import EventList from "./components/EventList";
import ScrollButton from "./components/ScrollButton";

let lastPage=0;
let pageSize=20;
let noMoreProducts=false;

class App extends Component {

  state = {
    products: null,
    events: null
  };

  componentDidMount() {
    this.setState({loading:true} );
    this.getEvents();
    this.getProducts(true);
    window.addEventListener("scroll", this.handleScroll);
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.handleScroll);
  }

  handleScroll = () => {
    const { innerHeight } = window;
    const { scrollHeight } = document.body;
    // IE에서는 document.documentElement 를 사용.
    const scrollTop =
      (document.documentElement && document.documentElement.scrollTop) ||
      document.body.scrollTop;

    if (scrollHeight - innerHeight - scrollTop < 100) {
      this.getProducts(false);
    }
  };

  getEvents = () => {
    axios.get('https://api.smartconsumer.kr/api/getEvents?&size=' + pageSize)
      .then(res => {
        const event_list = res.data;
        this.setState({ events: event_list});
      });
  };

  getProducts = (init) => {
    const { products } = this.state;

    if (!noMoreProducts) {
      this.setState({loading:true} );
      if (products && !init) {
        axios.get('https://api.smartconsumer.kr/api/getProducts?page=' + (lastPage+1) + '&size=' + pageSize)
          .then(res => {
            const product_list = res.data;
            if (product_list.products.length > 0) {
              product_list.products.map(item =>
                products.products.push(item)
              );
              this.setState({
                products: products
              });
            } else {
              noMoreProducts = true;
            }
          });
      } else {
        lastPage = 0;
        axios.get('https://api.smartconsumer.kr/api/getProducts?page=' + (lastPage+1) + '&size=' + pageSize)
          .then(res => {
            const product_list = res.data;
            this.setState({ products: product_list});
          });
      }
      this.setState({loading:false});

      if (!noMoreProducts) {
        lastPage = lastPage + 1;
      }
    }
  };
  
  render() {
    const { products, events, loading } = this.state;
    return (
      <div>
        <Header onRefresh={this.getProducts} cookies={this.props.cookies}/>
        <main>
          <div className="home">
            {
              events &&
              <EventList
                data={events.products}
              />
            }
            {
              products &&
              <ProductList
                data={products.products}
              />
            }
            <h1 align="center" hidden={!loading}>상품정보를 불러오고 있습니다...</h1>
          </div>
          <ScrollButton scrollStepInPx="200" delayInMs="0"/>
        </main>
      </div>
    );
  }
}

export default App;
