import React from 'react';

class ProductInfo extends React.Component {
  shouldComponentUpdate(nextProps, nextState) {
    // 수정 상태가 아니고, info 값이 같다면 리렌더링 안함
    if (nextProps.item === this.props.item) {
      return false;
    }
    // 나머지 경우엔 리렌더링함
    return true;
  }

  render(){
    const { item } = this.props;

    return (
      <li className="item">
        <a href={item.link} className="anchor" target="_blank" rel="noopener noreferrer">
          <div className="image">
            <div className="thumbnail"><img alt={item.title} src={item.image}/></div>
            <div className="sticker">
              <div className="mall" mall={item.mallName}/>
            </div>
          </div>
          <div className="desc">
            <p className="title">{item.title}</p>
            <div className="price_area">
              <strong className="price_desc">{item.discount}</strong>
              <span className="original_price">
                <i className="num">{item.orgPrice}</i>
              </span>
              <span className="price">
                <i className="num">{item.price}</i>
              </span>
            </div>
          </div>
        </a>
      </li>
    );
  }
}

export default ProductInfo;
